.card {
  // perspective must defined on parent element;
  // functionality
  perspective: 150rem;
  -moz-perspective: 150rem;
  position: relative;
  height: 52rem; //all of the children elements are absolute position card will get no height

  &__side {
    font-size: 2rem;
    height: 52rem;
    transition: all .8s ease;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden; // shold be added with safari
    border-radius: 3px;
    overflow: hidden; //if card's image overflowing it will be hidden
    box-shadow: 0 1.5rem 4rem rgba($color-black, .15);

    &--front {
      background-color: $color-white;
    }

    &--back {
      transform: rotateY(-180deg);

      &-1 {
        background-image: linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark);
      }

    }
  }
  &:hover &__side--front {
    // .card:hover .card__side {
    transform: rotateY(180deg); // so we can make flipping more natural
  }

  &:hover &__side--back {
    // .card:hover .card__side {
    transform: rotateY(0deg);
  }


  // FRONT SIDE ELEMENT

  &__picture {
    background-size: cover;
    height: 23rem;
    background-blend-mode: screen;
    clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);

    &-1 {
      background-image: linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark), url(../img/nat-5.jpg);
    } 

    &-2 {
      background-image: url(../img/nat-6.jpg);
    } 

    &-3 {
      background-image: url(../img/nat-7.jpg);
    } 
  }

  &__heading {
    font-size: 2.8rem;
    font-weight: 300;
    text-transform: uppercase;
    text-align: right;
    color: $color-white;
    position: absolute;
    top: 12rem;
    right: 2rem;
    width: 75%;
  }

  &__heading-span {
    padding: 1rem 1.5rem;
    box-decoration-break: clone;
    -webkit-box-decoration-break: clone;

    &--1 {
      background-image: linear-gradient(to right bottom, 
        rgba($color-secondary-light, 0.85),
        rgba($color-secondary-dark, 0.85));

    }

  }
  &__details {
    padding: 3rem;

    
    ul {
      list-style: none;
      width: 80%;
      margin: 0 auto;

      li {
        text-align: center;
        font-size: 1.5rem;
        padding: 1rem;

        &:not(:last-child) {
          border-bottom: 1px solid $color-grey-light-2;
        }
      }
    }
  }


}
